.Header {
  font-family: 'Roboto Mono', monospace;
  padding: 15px 0;

  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__left{
    text-decoration: none;
    color: #000000;
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__logo-main {
    margin: 0 auto;
    padding: 0;
    font-size: 22px;
    font-weight: bold;
  }

  &__logo-secondary {
    margin: 0 auto;
    padding: 0;
    font-size: 10px;
    font-weight: lighter;
    letter-spacing: 0.37rem;
  }

  &__search {
    margin-right: 20px;
  }

  &__input {
    outline: none;
    height: 20px;
    border-radius: 5px;
    border: 1px solid #000000;
    padding: 5px 5px 5px 40px;
    background: url('https://res.cloudinary.com/dum2a6djw/image/upload/c_scale,w_25/v1551519983/search_icon.png')
      no-repeat 5% center;
  }

  &__navigation {
    margin-top: 20px;
    text-transform: uppercase;
    border-top: 1px solid #c4c4c4;
    border-bottom: 1px solid #c4c4c4;
    padding: 10px 0;
  }

  &__nav-link {
    font-family: Roboto, sans-serif;
    font-weight: lighter;
    color: #000000;
    text-decoration: none;

    &:not(:last-child) {
      margin-right: 10px;
    }

    &_active {
      font-weight: bold;
    }
  }

  &__write-blog {
    color: #000000;
    text-decoration: none;
    margin-right: 20px;
    position: relative;
    text-align: left;
    vertical-align: middle;
    font-family: Roboto, sans-serif;
    font-weight: 600;
    border: 1px solid #000000;
    border-radius: 5px;
    background: none;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
    font-size: 15px;
    height: 30px;
    width: 155px;

    &:after {
      content: '';
      position: absolute;
      top: 6px;
      right: 7px;
      display: block;
      width: 20px;
      height: 20px;
      background: url('https://res.cloudinary.com/dum2a6djw/image/upload/v1551362952/rocket_icon.svg');
      background-size: contain;
    }

    &_centered {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__user {
    color: transparent;
    position: relative;
    width: 35px;
    height: 35px;

    &:after {
      position: absolute;
      top: 0;
      content: '';
      display: block;
      width: 35px;
      height: 35px;
      background: url('https://res.cloudinary.com/dum2a6djw/image/upload/v1551362952/user_icon.svg');
      background-size: contain;
    }
  }
}
